  font-weight: 400;
  font-display: auto;
  src: url("../../fonts/fa-regular-400.woff") format("woff"),
       url("../../fonts/fa-regular-400.svg#fontawesome") format("svg");
}

@font-face {
  font-family: 'Font Awesome 5 Free';
  font-style: normal;
  font-weight: 900;
  font-display: auto;
  src: url("../../fonts/fa-solid-900.woff") format("woff"),
